/* Century Gothic */

@font-face {
	font-family: 'Gothic';
	font-weight: 400;
	font-style: normal;
	src:url('./commercial/340C7B_0_0.eot');
	src:url('./commercial/340C7B_0_0.eot#iefix') format('embedded-opentype'),
		url('./commercial/340C7B_0_0.woff2') format('woff2'),
